    methods: {
      scrollTo (id) {
        let elem = this.$el.querySelector('#' + id)
        if (elem) {
          Velocity(elem, 'scroll', {
            duration: 750,
            easing: 'easeOutCube',
          })
        }
      },
    },
  }
